.footer {
    background-color: #f2f2f2;
    padding: 20px;
  }
  
  .footer__content {
    display: flex;
    justify-content: space-between;
  }
  
  .footer__section {
    margin-right: 40px;
  }
  
  .footer h3 {
    margin-bottom: 10px;
  }
  
  .footer ul {
    padding: 0;
    list-style-type: none;
  }
  
  .footer__bottom {
    margin-top: 20px;
    text-align: center;
  }